
.form-title {
    margin-bottom: 20px;
}

.form-input {
    border: 1px solid #a1a1a1;
    padding: .5rem;
    width: 100%;
    margin-top: 10px;
}

.form-input:focus {
    outline: none;
    border-color: #000;
}

.form-div {
    margin: 10px 0;
}

.form-button  {
    padding: .5rem;
    border: 1px solid #000;
    color: #fff;
    background-color: #000;
    text-align: center;
    text-transform: uppercase;
}